"use client"
import React, { useState } from 'react';

const CaseStudySection = () => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const caseStudies = [
    {
      title: "Arrow cabs",
      subtitle: "Best car rental in Trivandrum",
      work: "Website / seo",
      leads: "5000+",
      traffic: "50%",
      seoScore: "90+",
      leadsLabel: "Leads devliverd",
      trafficLabel: "Organic Traffic Increase",
      seoLabel: "SEO Score"
    },
    // Add more case studies here as needed
    {
      title: "Digital Marketing Pro",
      subtitle: "Leading marketing agency",
      work: "Branding / Marketing",
      leads: "3000+",
      traffic: "75%",
      seoScore: "95+",
      leadsLabel: "Campaigns Delivered",
      trafficLabel: "Conversion Rate",
      seoLabel: "Client Satisfaction"
    }
  ];

  const nextSlide = () => {
    setCurrentIndex((prevIndex) => 
      prevIndex === caseStudies.length - 1 ? 0 : prevIndex + 1
    );
  };

  const prevSlide = () => {
    setCurrentIndex((prevIndex) => 
      prevIndex === 0 ? caseStudies.length - 1 : prevIndex - 1
    );
  };

  const currentStudy = caseStudies[currentIndex];

  return (
    <div className="-mt-70">
      <div className="flex justify-center">
        <div className="p-[3px] mt-10 bg-gradient-to-r from-[#FCCB90] via-[#ff99cc] to-[#D57EEB] max-w-6xl w-full rounded-2xl hover:bg-gradient-to-r">
          <div className="rounded-xl p-8 bg-black">
            {/* Headline */}
            <div className="text-center mb-6">
              <h1 className="text-4xl md:text-5xl font-bold">
                <span className="bg-gradient-to-r from-[#FCCB90] via-[#ff99cc] to-[#D57EEB] bg-clip-text text-transparent">
                  Our Case Studies
                </span>
              </h1>
              <p className="text-slate-200 font-bold mt-4 max-w-3xl mx-auto">
                We continue to assist organizations in overcoming their most pressing business challenges.<br></br>
                Read how we supported our clients to improve their performance.
              </p>
            </div>

            {/* Case Study Card */}
            <div className="relative gradient_bg_l">
              <div className="text-center mb-8">
                <div className="inline-block">
                  <h2 className="text-3xl md:text-4xl font-bold text-white mb-2">
                    {currentStudy.title}
                  </h2>
                  <p className="text-white/80 text-lg mb-4">
                    {currentStudy.subtitle}
                  </p>
                  <div className="flex items-center justify-center gap-2 text-white/70">
                    <span className="font-medium">Work :</span>
                    <span>{currentStudy.work}</span>
                  </div>
                </div>
              </div>

              {/* Metrics */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-8">
                <div className="text-center">
                  <div className="text-3xl md:text-4xl font-bold text-amber-200 mb-2">
                    {currentStudy.leads}
                  </div>
                  <div className="text-white/80 text-sm">
                    {currentStudy.leadsLabel}
                  </div>
                </div>
                <div className="text-center">
                  <div className="text-3xl md:text-4xl font-bold text-amber-200 mb-2">
                    {currentStudy.traffic}
                  </div>
                  <div className="text-white/80 text-sm">
                    {currentStudy.trafficLabel}
                  </div>
                </div>
                <div className="text-center">
                  <div className="text-3xl md:text-4xl font-bold text-amber-200 mb-2">
                    {currentStudy.seoScore}
                  </div>
                  <div className="text-white/80 text-sm">
                    {currentStudy.seoLabel}
                  </div>
                </div>
              </div>

              {/* Visit Site Button */}
              <div className="text-center mb-6">
                <button className="inline-flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg font-medium transition-colors">
                  <svg width="18" height="18" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14" />
                  </svg>
                  Visit site
                </button>
              </div>

              {/* Navigation Arrows */}
              <div className="flex justify-center gap-4">
                <button 
                  onClick={prevSlide}
                  className="w-12 h-12 rounded-full border border-white/30 flex items-center justify-center text-white hover:bg-white/10 transition-colors"
                >
                  <svg width="20" height="20" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                  </svg>
                </button>
                <button 
                  onClick={nextSlide}
                  className="w-12 h-12 rounded-full border border-white/30 flex items-center justify-center text-white hover:bg-white/10 transition-colors"
                >
                  <svg width="20" height="20" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                  </svg>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CaseStudySection;